<script>
import demo from './components/demo.vue'
import spec from './components/spec.vue'

export default {
  name: 'app',
  components: {
    demo,
    spec
  },
  data() {
    return {
      buttonLoading: false,
      listLoading: false,
      checkA: false,
      checkB: false,
      delA: false,
      delB: false,
      moveA: false,
      moveB: false,
      moveC: false,
      font: '',
      index: [],
      indexFlag: false,
      fonts: [
        {
          name: 'Â∞öÈõÖ',
          class: 'shangya'
        },
        {
          name: 'Â§©Áúü',
          class: 'tongzhen'
        },
        {
          name: 'ÈõÖÈªë',
          class: 'yahei'
        }
      ],
      activeFont: '',
      menuVisible: false
    }
  },
  computed: {
    menuTxt() {
      return this.menuVisible ? 'ÂÖ≥Èó≠' : 'ÁõÆÂΩï'
    }
  },
  mounted() {
    /* eslint-disable*/
    this.activeFont = window.localStorage.getItem('font') || 'shangya'

    this.generateIndex()

    setTimeout(() => {
      this.listLoading = true
    }, 1000)
  },
  methods: {
    goGithub() {
      window.location.href = 'https://github.com/waynecz/UX-easy-guide'
    },
    toggleMenu() {
      this.menuVisible = !this.menuVisible
    },
    toggleNav(e) {
      let radio = document.getElementById(e.currentTarget.getAttribute('for'))
      if (radio.checked) {
        let fakeRadio = document.getElementById('fakeRadio')
        fakeRadio.click()
        console.log(fakeRadio.checked)
      }
    },
    selectAll() {
      this.$refs.input.select()
    },
    generateIndex() {
      let h2 = this.$refs.app.querySelectorAll('h2')
      h2.forEach((e, i) => {
        e.id = `q${i}`
        let _h2 = {
          text: e.innerText.replace('Ôºö', ''),
          id: e.id
        }

        let h4 = e.parentNode.querySelectorAll('h4')

        h4.forEach((ele, idx) => {
          ele.id = `${e.id}w${idx}`
          !_h2.children && (_h2.children = [])
          _h2.children.push({
            text: ele.innerText.replace('', ''),
            id: ele.id
          })
        })
        this.index.push(_h2)
      })
      this.indexFlag = true
    },
    changeFont(name) {
      this.activeFont = name
      window.localStorage.setItem('font', this.activeFont)
    }
  },
  watch: {
    listLoading(val) {
      if (val) {
        setTimeout(() => {
          this.listLoading = false
        }, 3000)
      } else {
        setTimeout(() => {
          this.listLoading = true
        }, 2000)
      }
    }
  }
}
</script>

